.icon-hear {
  fill: red;
  align-items: center;
  animation: animate125 3s 1s infinite;
  transform: scale(1.4);
}

@keyframes animate125 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }

  100% {
    transform: scale(1);
  }
}

.container {
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

.footer-modal {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0);
  transition: all .4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-44.5%)scale(0);
}

.footer-modal.active {
  background-color: rgba(0, 0, 0, .8);
  transform: translateY(0%)scale(100%);
}

.footer-contaiter {
  display: block;
}

.footer-body {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  margin: 50px;
  padding: 30px 10px;
  position: relative;
}

.footer-modal-close {
  cursor: pointer;
  font-size: 21px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.footer-foto {
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.footer-item {
  list-style: none;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.footer-foto-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.footer-foto-img {
  position: relative;
  overflow: hidden;
}

.footer-item h2 {
  margin: 5px;
  font-size: 15px;
}

.footer-item p {
  margin: 5px;
  font-size: 10px;
}

